*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-family: Montserrat, sans-serif;
  font-size: 62.5%;
}

@media screen and (max-width: 43.75em) {
  html {
    font-size: 50%;
  }
}

body {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

#hero {
  height: 100vh;
  align-items: center;
  display: flex;
}

#hero .container {
  animation: slideInLeft 1.2s ease-in;
}

#hero .container h1 {
  color: #343a40;
  font-size: 6rem;
  font-weight: 600;
}

#hero button {
  animation: slideUp 1s ease-in .7s;
}

section {
  padding: 8rem 10rem 12rem;
  transition: transform 1s, opacity 1s;
}

.about {
  clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
  background-image: linear-gradient(135deg, #02aab0, #00cdac);
}

@media screen and (max-width: 34.375em) {
  .about {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

.about__info {
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin-top: 3rem;
  display: grid;
}

@media screen and (max-width: 34.375em) {
  .about__info {
    grid-template-columns: 7fr 3fr;
  }
}

.about__image img {
  object-fit: cover;
  width: 65%;
  display: inline-block;
}

.about__description {
  color: #f1f3f5;
  font-size: 1.6rem;
  line-height: 1.5;
}

.projects h1.primary-heading {
  color: #343a40;
}

.projects__grid {
  gap: 6rem;
  margin-top: 6rem;
  display: grid;
}

.projects__row {
  align-items: center;
  gap: 6rem;
  display: grid;
}

.projects__row:nth-child(2n) {
  grid-template-columns: 1fr 1fr;
}

@media screen and (max-width: 43.75em) {
  .projects__row:nth-child(2n) {
    grid-template-columns: 3fr 7fr;
  }
}

.projects__row:nth-child(2n+1) {
  grid-template-columns: 1fr 1fr;
}

@media screen and (max-width: 43.75em) {
  .projects__row:nth-child(2n+1) {
    grid-template-columns: 7fr 3fr;
  }
}

.projects__image {
  cursor: pointer;
}

.projects__image img {
  object-fit: cover;
  width: 100%;
  filter: grayscale(80%);
  border-radius: 15px;
  transition: transform .7s;
  display: inline-block;
}

.projects__image:hover img {
  transform: scale(1.3);
}

.projects__description {
  flex-direction: column;
  align-items: start;
  gap: 2rem;
  display: flex;
}

.projects__description p {
  font-size: 1.8rem;
}

.projects__description h2 {
  text-transform: uppercase;
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.contact {
  clip-path: polygon(0 15vh, 100% 0%, 100% 100%, 0 100%);
  background-color: #333;
  padding: 14rem 10rem 10rem;
}

.contact__btn {
  width: 50%;
  border-bottom: 1px solid #e9ecef;
  justify-content: center;
  gap: 2.8rem;
  margin: 5rem auto 0;
  padding-bottom: 2.5rem;
  display: flex;
}

.contact__btn svg {
  width: 4rem;
  height: 4rem;
  fill: #fff;
  transition: all .3s;
}

.contact__btn svg:hover {
  transform: translateY(-.5rem);
}

.cta {
  cursor: pointer;
  background-color: #0000;
  background-size: 230%;
  font-size: 1.7rem;
  font-weight: 600;
  transition: all .3s;
}

.cta--green {
  border: 2px solid #00cdac;
}

.cta--white {
  border: 2px solid #fff;
}

.cta a {
  padding: 1rem 2.5rem;
  display: inline-block;
}

.cta--green {
  background-image: linear-gradient(135deg, #0000 0% 50%, #02aab0 50%, #00cdac 100%);
}

.cta--green:hover a {
  color: #fff;
}

.cta--white {
  background-image: linear-gradient(135deg, #0000 0% 50%, #fff 50% 100%);
}

.cta--white:hover a {
  color: #00cdac;
}

.cta:hover {
  background-position: 100%;
}

.text-color {
  -webkit-background-clip: text;
  color: #0000;
}

.text-color--green {
  background-image: linear-gradient(135deg, #02aab0, #00cdac);
}

.text-color--white {
  background-image: linear-gradient(135deg, #fff, #fff);
}

.mt-3 {
  margin-top: 3rem;
}

.primary-heading {
  text-transform: uppercase;
  color: #f1f3f5;
  text-align: center;
  font-size: 3.5rem;
}

.slideIn {
  opacity: 1;
  transform: translateX(0);
}

@keyframes slideInLeft {
  0% {
    opacity: 0;
    transform: translateX(-50%);
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(50%);
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.section--hidden {
  opacity: 0;
  transform: translateY(8rem);
}

.left {
  opacity: 0;
  transform: translateX(-4rem);
}

.right {
  opacity: 0;
  transform: translateX(4rem);
}

.transition {
  transition: transform 1s, opacity 1s;
}

